import React, { useMemo, useState } from 'react';
import { Calculator, Info, BarChart3, AlertTriangle } from 'lucide-react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Cell, ErrorBar } from 'recharts';

export default function BiometricCICalculator() {
  const [numSubjects, setNumSubjects] = useState(256);
  const [retries, setRetries] = useState(0); // NEW: retries per genuine subject
  const [trueAccepts, setTrueAccepts] = useState(246);
  const [falseAccepts, setFalseAccepts] = useState(50);
  const [confidenceLevel, setConfidenceLevel] = useState(95);

  const [results, setResults] = useState(null);
  const [errors, setErrors] = useState([]);
  const [showChart, setShowChart] = useState(true);
  
  // Robust Wilson Score Interval with guards
  const wilsonCI = (successes: number, trials: number, confidence: number) => {
    if (!Number.isFinite(trials) || trials <= 0) {
      // No data → center undefined; return 0..1 to show maximal uncertainty
      return { lower: 0, upper: 1, center: 0 };
    }
    const clToZ: Record<number, number> = { 90: 1.645, 95: 1.96, 99: 2.576 };
    const z = clToZ[confidence] ?? 1.96;
    const s = Math.max(0, Math.min(successes, trials)); // clamp
    const p = s / trials;
    const z2 = z * z;

    const denom = 1 + z2 / trials;
    const centerAdj = (p + z2 / (2 * trials)) / denom;
    const halfWidth =
      (z * Math.sqrt((p * (1 - p)) / trials + z2 / (4 * trials * trials))) / denom;

    const lower = Math.max(0, Math.min(1, centerAdj - halfWidth));
    const upper = Math.max(0, Math.min(1, centerAdj + halfWidth));
    return { lower, upper, center: p };
  };

  const impostorComparisons = useMemo(
    () => Math.max(0, numSubjects * numSubjects - numSubjects),
    [numSubjects]
  );

  const genuineComparisons = useMemo(
    // Each subject can retry "retries" extra times → attempts per subject = 1 + retries
    () => Math.max(0, numSubjects * Math.max(1, 1 + retries)),
    [numSubjects, retries]
  );

  const validate = () => {
    const errs: string[] = [];
    if (!Number.isFinite(numSubjects) || numSubjects < 1) {
      errs.push('Number of subjects must be at least 1.');
    }
    if (!Number.isFinite(retries) || retries < 0 || !Number.isInteger(retries)) {
      errs.push('Retries must be a whole number ≥ 0.');
    }
    if (!Number.isFinite(trueAccepts) || trueAccepts < 0) {
      errs.push('True accepts must be ≥ 0.');
    }
    if (!Number.isFinite(falseAccepts) || falseAccepts < 0) {
      errs.push('False accepts must be ≥ 0.');
    }
    if (trueAccepts > genuineComparisons) {
      errs.push(`True accepts (${trueAccepts}) cannot exceed genuine attempts (${genuineComparisons}).`);
    }
    if (falseAccepts > impostorComparisons) {
      errs.push(`False accepts (${falseAccepts}) cannot exceed impostor attempts (${impostorComparisons}).`);
    }
    return errs;
  };

  const calculateMetrics = () => {
    const errs = validate();
    setErrors(errs);
    if (errs.length) {
      setResults(null);
      return;
    }

    const falseRejects = genuineComparisons - trueAccepts;
    const trueRejects = impostorComparisons - falseAccepts;

    const gar = wilsonCI(trueAccepts, genuineComparisons, confidenceLevel);
    const frr = wilsonCI(falseRejects, genuineComparisons, confidenceLevel);
    const far = wilsonCI(falseAccepts, impostorComparisons, confidenceLevel);
    const trr = wilsonCI(trueRejects, impostorComparisons, confidenceLevel);

    setResults({
      genuineComparisons,
      impostorComparisons,
      trueAccepts,
      falseRejects,
      falseAccepts,
      trueRejects,
      gar,
      frr,
      far,
      trr
    });
  };

  const formatPercent = (value: number, decimals = 2) => `${(value * 100).toFixed(decimals)}%`;
  const formatCI = (metric: any, decimals = 2) =>
    `[${formatPercent(metric.lower, decimals)}, ${formatPercent(metric.upper, decimals)}]`;

  const prepareErrorRateData = () => {
    if (!results) return [];
    return [
      {
        name: 'FRR',
        value: results.frr.center * 100,
        lower: results.frr.lower * 100,
        upper: results.frr.upper * 100,
        err: [results.frr.lower * 100, results.frr.upper * 100],
        fill: '#f59e0b',
        description: 'False Reject Rate'
      },
      {
        name: 'FAR',
        value: results.far.center * 100,
        lower: results.far.lower * 100,
        upper: results.far.upper * 100,
        err: [results.far.lower * 100, results.far.upper * 100],
        fill: '#ef4444',
        description: 'False Accept Rate'
      }
    ];
  };

  const prepareAccuracyData = () => {
    if (!results) return [];
    return [
      {
        name: 'GAR',
        value: results.gar.center * 100,
        lower: results.gar.lower * 100,
        upper: results.gar.upper * 100,
        err: [results.gar.lower * 100, results.gar.upper * 100],
        fill: '#10b981',
        description: 'Genuine Accept Rate'
      },
      {
        name: 'TRR',
        value: results.trr.center * 100,
        lower: results.trr.lower * 100,
        upper: results.trr.upper * 100,
        err: [results.trr.lower * 100, results.trr.upper * 100],
        fill: '#3b82f6',
        description: 'True Reject Rate'
      }
    ];
  };

  const CustomTooltip = ({ active, payload }: any) => {
    if (active && payload && payload.length) {
      const data = payload[0].payload;
      return (
        <div className="bg-white p-3 border-2 border-gray-300 rounded-lg shadow-lg">
          <p className="font-semibold text-gray-800">{data.name}</p>
          <p className="text-xs text-gray-600 mb-2">{data.description}</p>
          <p className="text-sm">
            <span className="font-semibold">Value:</span> {data.value.toFixed(2)}%
          </p>
          <p className="text-sm text-gray-600">
            CI: [{data.lower.toFixed(2)}%, {data.upper.toFixed(2)}%]
          </p>
        </div>
      );
    }
    return null;
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 p-8">
      <div className="max-w-5xl mx-auto">
        <div className="bg-white rounded-lg shadow-xl p-8">
          <div className="flex items-center gap-3 mb-6">
            <Calculator className="w-8 h-8 text-indigo-600" />
            <h1 className="text-3xl font-bold text-gray-800">
              Biometric Confidence Interval Calculator
            </h1>
          </div>

          <div className="bg-blue-50 border-l-4 border-blue-500 p-4 mb-6 rounded">
            <div className="flex items-start gap-2">
              <Info className="w-5 h-5 text-blue-600 mt-0.5 flex-shrink-0" />
              <p className="text-sm text-blue-900">
                Calculate confidence intervals for biometric performance metrics using the Wilson Score method. 
                Enter the number of subjects, retries per subject (for genuine attempts), and observed results at a chosen threshold.
              </p>
            </div>
          </div>

          {/* Input Section */}
          <div className="grid md:grid-cols-2 gap-6 mb-8">
            <div className="space-y-4">
              <h2 className="text-xl font-semibold text-gray-700 mb-4">Test Configuration</h2>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Number of Subjects
                </label>
                <input
                  type="number"
                  value={numSubjects}
                  onChange={(e) => setNumSubjects(Math.max(1, parseInt(e.target.value || '0', 10)))}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                  min={1}
                />
                <p className="text-xs text-gray-500 mt-1">
                  Impostor attempts: {impostorComparisons.toLocaleString()}
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Retries per Genuine Subject
                </label>
                <input
                  type="number"
                  value={retries}
                  onChange={(e) =>
                    setRetries(Math.max(0, Math.floor(Number(e.target.value ?? 0))))
                  }
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                  min={0}
                />
                <p className="text-xs text-gray-500 mt-1">
                  Genuine attempts: {genuineComparisons.toLocaleString()} ({numSubjects} × {1 + Math.max(0, retries)})
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Confidence Level
                </label>
                <select
                  value={confidenceLevel}
                  onChange={(e) => setConfidenceLevel(parseInt(e.target.value, 10))}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                >
                  <option value="90">90%</option>
                  <option value="95">95%</option>
                  <option value="99">99%</option>
                </select>
              </div>
            </div>

            <div className="space-y-4">
              <h2 className="text-xl font-semibold text-gray-700 mb-4">Observed Results</h2>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  True Accepts (Genuine Accepted)
                </label>
                <input
                  type="number"
                  value={trueAccepts}
                  onChange={(e) => {
                    const v = Math.max(0, parseInt(e.target.value || '0', 10));
                    setTrueAccepts(v);
                  }}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                  min={0}
                />
                <p className="text-xs text-gray-500 mt-1">
                  False Rejects (computed): {Math.max(0, genuineComparisons - Math.max(0, trueAccepts))}
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  False Accepts (Impostor Accepted)
                </label>
                <input
                  type="number"
                  value={falseAccepts}
                  onChange={(e) => {
                    const v = Math.max(0, parseInt(e.target.value || '0', 10));
                    setFalseAccepts(v);
                  }}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                  min={0}
                />
                <p className="text-xs text-gray-500 mt-1">
                  True Rejects (computed): {Math.max(0, impostorComparisons - Math.max(0, falseAccepts)).toLocaleString()}
                </p>
              </div>
            </div>
          </div>

          {/* Validation errors */}
          {errors.length > 0 && (
            <div className="mb-6 rounded-md border border-yellow-400 bg-yellow-50 p-4 text-yellow-900">
              <div className="flex items-start gap-2">
                <AlertTriangle className="w-5 h-5 mt-0.5" />
                <div>
                  <p className="font-semibold mb-1">Please fix the following:</p>
                  <ul className="list-disc pl-5 space-y-1 text-sm">
                    {errors.map((e, i) => <li key={i}>{e}</li>)}
                  </ul>
                </div>
              </div>
            </div>
          )}

          <button
            onClick={calculateMetrics}
            className="w-full bg-indigo-600 hover:bg-indigo-700 text-white font-semibold py-3 px-6 rounded-lg transition-colors duration-200 shadow-md hover:shadow-lg disabled:opacity-50"
            disabled={errors.length > 0}
          >
            Calculate Confidence Intervals
          </button>

          {/* Results Section */}
          {results && (
            <div className="mt-8 space-y-6">
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-2xl font-bold text-gray-800">Results</h2>
                <button
                  onClick={() => setShowChart(!showChart)}
                  className="flex items-center gap-2 px-4 py-2 bg-indigo-100 hover:bg-indigo-200 text-indigo-700 rounded-lg transition-colors"
                >
                  <BarChart3 className="w-4 h-4" />
                  {showChart ? 'Hide Chart' : 'Show Chart'}
                </button>
              </div>

              {showChart && (
                <div className="space-y-6 mb-6">
                  {/* Error Rates Chart */}
                  <div className="bg-white border-2 border-red-200 rounded-lg p-6">
                    <h3 className="text-lg font-semibold text-gray-800 mb-2">
                      Error Rates (Lower is Better)
                    </h3>
                    <p className="text-sm text-gray-600 mb-4">
                      FRR and FAR with {confidenceLevel}% confidence intervals
                    </p>
                    <ResponsiveContainer width="100%" height={350}>
                      <BarChart data={prepareErrorRateData()} margin={{ top: 20, right: 30, left: 20, bottom: 60 }}>
                        <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb" />
                        <XAxis 
                          dataKey="name" 
                          tick={{ fill: '#374151', fontSize: 14, fontWeight: 600 }}
                          label={{ value: 'Error Metric', position: 'insideBottom', offset: -10, style: { fill: '#6b7280', fontWeight: 600 } }}
                        />
                        <YAxis 
                          tick={{ fill: '#374151', fontSize: 12 }}
                          label={{ value: 'Error Rate (%)', angle: -90, position: 'insideLeft', style: { fill: '#6b7280', fontWeight: 600 } }}
                        />
                        <Tooltip content={<CustomTooltip />} />
                        <Bar dataKey="value" radius={[8, 8, 0, 0]}>
                          {prepareErrorRateData().map((entry: any, index: number) => (
                            <Cell key={`cell-e-${index}`} fill={entry.fill} />
                          ))}
                          {/* Use paired low/high array for ErrorBar for clarity */}
                          <ErrorBar dataKey="err" direction="y" strokeWidth={2} stroke="#374151" />
                        </Bar>
                      </BarChart>
                    </ResponsiveContainer>
                    <div className="flex items-center justify-center gap-6 mt-2">
                      <div className="flex items-center gap-2">
                        <div className="w-4 h-4 bg-yellow-500 rounded"></div>
                        <span className="text-xs text-gray-600">FRR: Genuine users rejected</span>
                      </div>
                      <div className="flex items-center gap-2">
                        <div className="w-4 h-4 bg-red-500 rounded"></div>
                        <span className="text-xs text-gray-600">FAR: Impostors accepted</span>
                      </div>
                    </div>
                  </div>

                  {/* Accuracy Rates Chart */}
                  <div className="bg-white border-2 border-green-200 rounded-lg p-6">
                    <h3 className="text-lg font-semibold text-gray-800 mb-2">
                      Accuracy Rates (Higher is Better)
                    </h3>
                    <p className="text-sm text-gray-600 mb-4">
                      GAR and TRR with {confidenceLevel}% confidence intervals
                    </p>
                    <ResponsiveContainer width="100%" height={350}>
                      <BarChart data={prepareAccuracyData()} margin={{ top: 20, right: 30, left: 20, bottom: 60 }}>
                        <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb" />
                        <XAxis 
                          dataKey="name" 
                          tick={{ fill: '#374151', fontSize: 14, fontWeight: 600 }}
                          label={{ value: 'Accuracy Metric', position: 'insideBottom', offset: -10, style: { fill: '#6b7280', fontWeight: 600 } }}
                        />
                        <YAxis 
                          domain={[90, 100]}
                          tick={{ fill: '#374151', fontSize: 12 }}
                          label={{ value: 'Accuracy Rate (%)', angle: -90, position: 'insideLeft', style: { fill: '#6b7280', fontWeight: 600 } }}
                        />
                        <Tooltip content={<CustomTooltip />} />
                        <Bar dataKey="value" radius={[8, 8, 0, 0]}>
                          {prepareAccuracyData().map((entry: any, index: number) => (
                            <Cell key={`cell-a-${index}`} fill={entry.fill} />
                          ))}
                          <ErrorBar dataKey="err" direction="y" strokeWidth={2} stroke="#374151" />
                        </Bar>
                      </BarChart>
                    </ResponsiveContainer>
                    <div className="flex items-center justify-center gap-6 mt-2">
                      <div className="flex items-center gap-2">
                        <div className="w-4 h-4 bg-green-500 rounded"></div>
                        <span className="text-xs text-gray-600">GAR: Genuine users accepted</span>
                      </div>
                      <div className="flex items-center gap-2">
                        <div className="w-4 h-4 bg-blue-500 rounded"></div>
                        <span className="text-xs text-gray-600">TRR: Impostors rejected</span>
                      </div>
                    </div>
                  </div>
                </div>
              )}

              {/* Summary Stats */}
              <div className="grid md:grid-cols-4 gap-4 mb-6">
                <div className="bg-gray-50 p-4 rounded-lg">
                  <p className="text-xs text-gray-600 mb-1">Genuine Attempts</p>
                  <p className="text-2xl font-bold text-gray-800">{results.genuineComparisons.toLocaleString()}</p>
                </div>
                <div className="bg-gray-50 p-4 rounded-lg">
                  <p className="text-xs text-gray-600 mb-1">Impostor Attempts</p>
                  <p className="text-2xl font-bold text-gray-800">{results.impostorComparisons.toLocaleString()}</p>
                </div>
                <div className="bg-green-50 p-4 rounded-lg">
                  <p className="text-xs text-green-700 mb-1">True Accepts</p>
                  <p className="text-2xl font-bold text-green-800">{results.trueAccepts.toLocaleString()}</p>
                </div>
                <div className="bg-red-50 p-4 rounded-lg">
                  <p className="text-xs text-red-700 mb-1">False Accepts</p>
                  <p className="text-2xl font-bold text-red-800">{results.falseAccepts.toLocaleString()}</p>
                </div>
              </div>

              {/* Metrics Table */}
              <div className="overflow-x-auto">
                <table className="w-full border-collapse">
                  <thead>
                    <tr className="bg-indigo-600 text-white">
                      <th className="py-3 px-4 text-left font-semibold">Metric</th>
                      <th className="py-3 px-4 text-right font-semibold">Point Estimate</th>
                      <th className="py-3 px-4 text-right font-semibold">{confidenceLevel}% CI</th>
                      <th className="py-3 px-4 text-left font-semibold">Interpretation</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-gray-200">
                    <tr className="bg-green-50 hover:bg-green-100 transition-colors">
                      <td className="py-3 px-4 font-semibold text-gray-800">
                        GAR
                        <span className="block text-xs text-gray-600 font-normal">Genuine Accept Rate</span>
                      </td>
                      <td className="py-3 px-4 text-right font-mono text-lg">
                        {formatPercent(results.gar.center)}
                      </td>
                      <td className="py-3 px-4 text-right font-mono text-sm">
                        {formatCI(results.gar)}
                      </td>
                      <td className="py-3 px-4 text-sm text-gray-700">User convenience</td>
                    </tr>

                    <tr className="bg-yellow-50 hover:bg-yellow-100 transition-colors">
                      <td className="py-3 px-4 font-semibold text-gray-800">
                        FRR
                        <span className="block text-xs text-gray-600 font-normal">False Reject Rate</span>
                      </td>
                      <td className="py-3 px-4 text-right font-mono text-lg">
                        {formatPercent(results.frr.center)}
                      </td>
                      <td className="py-3 px-4 text-right font-mono text-sm">
                        {formatCI(results.frr)}
                      </td>
                      <td className="py-3 px-4 text-sm text-gray-700">Genuine user rejections</td>
                    </tr>

                    <tr className="bg-red-50 hover:bg-red-100 transition-colors">
                      <td className="py-3 px-4 font-semibold text-gray-800">
                        FAR
                        <span className="block text-xs text-gray-600 font-normal">False Accept Rate</span>
                      </td>
                      <td className="py-3 px-4 text-right font-mono text-lg">
                        {formatPercent(results.far.center, 4)}
                      </td>
                      <td className="py-3 px-4 text-right font-mono text-sm">
                        {formatCI(results.far, 4)}
                      </td>
                      <td className="py-3 px-4 text-sm text-gray-700">
                        <span className="font-semibold text-red-700">Security metric</span>
                      </td>
                    </tr>

                    <tr className="bg-blue-50 hover:bg-blue-100 transition-colors">
                      <td className="py-3 px-4 font-semibold text-gray-800">
                        TRR
                        <span className="block text-xs text-gray-600 font-normal">True Reject Rate</span>
                      </td>
                      <td className="py-3 px-4 text-right font-mono text-lg">
                        {formatPercent(results.trr.center, 4)}
                      </td>
                      <td className="py-3 px-4 text-right font-mono text-sm">
                        {formatCI(results.trr, 4)}
                      </td>
                      <td className="py-3 px-4 text-sm text-gray-700">Impostor rejection</td>
                    </tr>
                  </tbody>
                </table>
              </div>

              {/* Insights */}
              <div className="bg-indigo-50 border-l-4 border-indigo-500 p-4 rounded mt-6">
                <h3 className="font-semibold text-indigo-900 mb-2">Key Insights</h3>
                <ul className="space-y-2 text-sm text-indigo-800">
                  <li>
                    <strong>Relative Precision (GAR):</strong> CI width = {formatPercent(results.gar.upper - results.gar.lower)} 
                    ({results.gar.center > 0 ? (((results.gar.upper - results.gar.lower) / (2 * results.gar.center)) * 100).toFixed(1) : '0'}% relative)
                  </li>
                  <li>
                    <strong>Relative Precision (FAR):</strong> CI width = {formatPercent(results.far.upper - results.far.lower, 4)} 
                    ({results.far.center > 0 ? (((results.far.upper - results.far.lower) / (2 * results.far.center)) * 100).toFixed(1) : '0'}% relative)
                  </li>
                  <li>
                    <strong>Sample Ratio:</strong> {(impostorComparisons / Math.max(1, genuineComparisons)).toFixed(0)}× more impostor than genuine attempts → FAR typically tighter.
                  </li>
                </ul>
              </div>
            </div>
          )}
        </div>

        {/* Footer */}
        <div className="mt-6 text-center text-sm text-gray-600">
          Wilson Score intervals are clamped to [0, 1]. Inputs are validated so counts never exceed available attempts.
        </div>
      </div>
    </div>
  );
}
